
import { defineComponent } from 'vue';

export default defineComponent({

    name: 'Botao',

    emits: ['clicado'],

    props: {
        cronometroRodando: Boolean,
        texto: {
            type: String,
            required: true
        },
        
        icone: {
            type: String,
            required: true
        }
    },

    methods: {
        clicado() {
            this.$emit('clicado');
        }
    }

}); 
